body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.selected {
  -webkit-filter: grayscale(100%) brightness(40%) sepia(100%) hue-rotate(-50deg) saturate(600%) contrast(0.8);
          filter: grayscale(100%) brightness(40%) sepia(100%) hue-rotate(-50deg) saturate(600%) contrast(0.8); }

.disabled {
  -webkit-filter: grayscale(100%);
          filter: grayscale(100%); }

.hero {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: flex-end;
          align-items: flex-end;
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat;
  width: 100%; }
  .hero > span {
    display: block;
    color: white;
    background: rgba(0, 0, 0, 0.5);
    width: 100%;
    padding: 5px;
    text-align: center; }
  .hero:hover {
    opacity: .3; }

header, footer {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  background: #1e2a38;
  color: white; }
  header strong, footer strong {
    padding-left: 10px;
    display: block; }

header {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 1; }

.hero-list {
  background: #1e2a38; }

main {
  background: #4D5B6C; }

.chessboard .square {
  display: -webkit-flex;
  display: flex; }

.chessboard > * {
  background-color: #7a8545; }

.chessboard > *:nth-child(16n + 2) {
  background-color: #565c32; }

.chessboard > *:nth-child(16n + 4) {
  background-color: #565c32; }

.chessboard > *:nth-child(16n + 6) {
  background-color: #565c32; }

.chessboard > *:nth-child(16n + 8) {
  background-color: #565c32; }

.chessboard > *:nth-child(16n + 9) {
  background-color: #565c32; }

.chessboard > *:nth-child(16n + 11) {
  background-color: #565c32; }

.chessboard > *:nth-child(16n + 13) {
  background-color: #565c32; }

.chessboard > *:nth-child(16n + 15) {
  background-color: #565c32; }

.synergy-item {
  margin: 20px;
  background-color: white;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23); }

.synergy-item-header .title {
  text-transform: capitalize;
  border-bottom: 2px solid #1e2a38;
  font-weight: bold; }

.synergy-item-header .synergy-item-count {
  font-size: 2.5rem; }

.synergy-item-body {
  border-left: 2px solid #1e2a38; }

.active-synergy {
  padding: 5px;
  background: lightgreen; }

.missing-synergy {
  padding: 5px;
  background: lightcoral; }

html, body, .container {
  width: 100%;
  height: 100%; }

#root {
  height: 100vh; }

.container {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 60px 1fr 60px;
  overflow: scroll; }

main {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: column wrap;
          flex-flow: column wrap;
  -webkit-align-items: center;
          align-items: center; }
  main > * {
    -webkit-flex: 1 1;
            flex: 1 1; }

.chessboard {
  display: grid;
  grid-template-columns: repeat(8, minmax(100px, 125px));
  grid-template-rows: repeat(4, 100px); }

.hero-list {
  display: grid;
  grid-template-columns: repeat(8, minmax(100px, 125px));
  grid-auto-rows: 100px;
  position: -webkit-sticky;
  position: sticky;
  bottom: -70vh; }

.synergy-list {
  display: grid;
  grid-template-columns: 1fr; }

.synergy-item {
  display: grid;
  grid-template-columns: 125px 1fr; }

.synergy-item-header {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: column wrap;
          flex-flow: column wrap; }
  .synergy-item-header .title {
    width: 100%;
    padding: 3px 5px;
    -webkit-flex-grow: 0;
            flex-grow: 0; }
  .synergy-item-header .synergy-item-count {
    -webkit-flex: 1 1;
            flex: 1 1;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center; }

.synergy-item-body {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: column wrap;
          flex-flow: column wrap; }
  .synergy-item-body > * {
    -webkit-flex: 1 1;
            flex: 1 1; }

